//
// Custom Thumbnails
// --------------------------------------------------

// Make thumbnails to fit entire container width
.thumbnail-block {
	display: block;

	> img,
	a > img {
		width: 100%;
		height: auto;
	}
}

.thumbnail,
.thumbnail-2 {
	> img,
	> img,
	a > img,
	a > img {
		display: block;
		max-width: 100%;
		height: auto;
		margin-left: auto;
		margin-right: auto;
	}
}

.thumbnail {
	padding: 0;
	border: none;
	background: transparent;

	.caption {
		margin-top: 54px;

		&-mod-1 {
			margin-top: 20px;

			* + a { margin-top: 10px; }

		}
		&-mod-2 {
			margin-top: 30px;
		}
		&-mod-3 {
			margin-top: 10px;
			@include media-breakpoint-up(xl) {
				padding: 0 200px;
			}
		}
		&-mod-4 {
			margin-top: 4px;
			.blog-post-body {
				margin-top: 15px;
			}
		}
	}
	a { display: inline-block; }

	&-mod-1 {
		max-width: 270px;
		margin-left: auto;
		margin-right: auto;
	}

	&-mod-2 {
		max-width: 370px;
		margin-left: auto;
		margin-right: auto;
	}

	span {
		display: inline-block;
	}

	h6 + span { margin-top: 5px; }
	span + p { margin-top: 15px; }
}

.thumbnail-1 {
	padding: 0;

	.caption {
		margin-top: 17px;
	}
}

.thumbnail-2 {
	@extend .thumbnail;
	position: relative;
	overflow: hidden;

	.caption {
		position: absolute;
		left: 50%;
		bottom: 0;
		width: 100%;
		margin-top: 0;
		transition: .4s;
		transform: translateX(-50%);

		.list-inline {
			margin-bottom: 10px;
		}

		@include media-breakpoint-up(xl) {
			transform: translateY(33%) translateX(-50%);

			h6 + span {
				margin-top: 0;
			}

			.list-inline {
				margin-top: 5px;
				margin-bottom: 17px;
				opacity: 0;
				transition: .4s;
			}
		}
	}

	h6 { color: $white-clr; }
	span,
	.fa-facebook,
	.fa-twitter,
	.fa-google-plus,
	.fa-pinterest-p,
	.fa-vimeo,
	.fa-google,
	.fa-rss { color: $gray; }

	.fa-facebook,
	.fa-twitter,
	.fa-google-plus,
	.fa-pinterest-p,
	.fa-vimeo,
	.fa-google,
	.fa-rss {
		&:hover {
			color: $white-clr;
		}
	}

	&:hover {
		.caption {
			transform: translateY(0) translateX(-50%);
			.list-inline { opacity: 1; }
		}
	}
}

.thumbnail-3 {
	.caption { padding: 30px; }
}

.thumbnail-gallery {
	position: relative;
	margin-bottom: 0;
	overflow: hidden;

	img {
		width: 100%;
	}

	.caption {
		position: absolute;
		top: 0;
		bottom: 0;
		left: 0;
		right: 0;
		opacity: 0;
		visibility: hidden;
		transition: .5s;
		background-color: rgba($primary, .58);

		&-wrapper {
			position: absolute;
			top: 50%;
			left: 50%;
			width: 90%;
			transform: translate(-50%, -100%);
			transition: .5s;
		}

		h4,
		p {
			margin-top: 6%;
		}

		p {
			display: none;
			@media (min-width: 1440px) {
				display: block;
			}
		}
		@media (max-width: 991px) {
			.icon-md { font-size: 20px; }
			h4 { font-size: 16px; }
		}

	}
	&:hover {
		.caption {
			opacity: 1;
			visibility: visible;
		}
		.caption-wrapper {
			transform: translate(-50%, -50%);
		}
	}
}

.cobbles-gallery {
	.thumbnail-gallery {
		@include media-breakpoint-up(sm) {
			width: 50%;
		}
		@include media-breakpoint-up(md) {
			width: 25%;
		}
	}
}

.masonry-gallery {
	&[data-isotope-layout] {
		[class*="col-"] { margin-top: 30px; }
	}

	.caption {
		left: 15px;
		right: 15px;
	}
}